import shoplist from '@/components/shoplist/shoplist';
import indexcoupon from '@/components/coupon/indexcoupon';
import nav from '../../components/nav/nav.vue';
import header2fix from '@/components/header/header2fix';
import { mapState, mapGetters } from 'vuex';
export default{
    name: 'mycollection',
    data() {
        return {
            tabs: [
                {
                    id: 0,
                    name: '商家',
                    isActive: true
                }, {
                    id: 1,
                    name: '优惠券',
                    isActive: false
                }],
            currentTab: 0,
            shopList: [],
            couponList: []
        };
    },
    computed: {
        ...mapState(['CouponListInfo', 'global', 'zoneId']),
        ...mapGetters([
            // 'filterUserCouponList'
        ])
    },
    beforeCreate() {

    },

    methods: {
        loadShopPage() {
            let that = this;
            this.axios({
                method: 'post',
                url: this.global.hftcomShop,
                data: '{"id":19,"jsonrpc":"2.0","method":"collectShop","params":{"userCode":"' + that.$store.state.userCode + '","latitude":' + that.$store.state.latitude + ',"longitude":' + that.$store.state.longitude + ',"zoneId": "' + this.zoneId + '"}}',
                contentType: 'application/json',
                dataType: 'json'
            }).then((result) => {
                console.log('收藏商家列表');
                if (result.data.result === 'null' || result.data.result === null) {
                    result.data.result = {};
                }
                if (result.data.result[0] === undefined) {
                    result.data.result[0] = [];
                }
                console.log(result.data.result[0]);
                        // 用于筛选商家显示的收藏按钮
                that.$store.state.collectShopList = result.data.result[0];
                    // 商家列表
                that.shopList = result.data.result[0];
            });
        },
        loadCouponPage() {
            let that = this;
            this.axios({
                method: 'post',
                url: this.global.hftcomShop,
                data: '{"id":19,"jsonrpc":"2.0","method":"showCoupon","params":{"userCode":"' + that.$store.state.userCode + '", "zoneId":"' + this.zoneId + '"}}',
                contentType: 'application/json',
                dataType: 'json'
            }).then((result) => {
                if (result.data.result === 'null') {
                    that.couponList = [];
                } else {
                    console.log(result.data.result);
                    var newlist = [];
                    for (var i = 0; i < result.data.result.length; i++) {
                        if (result.data.result[i][0] === undefined) {
                            result.data.result[i][0] = {'collectCouponCode': ''};
                        }
                        if (result.data.result[i][0]['collectCouponCode'] === '' || result.data.result[i][0]['collectCouponCode'] === undefined) {
                        } else {
                            result.data.result[i][0]['batchCouponCode'] = result.data.result[i][0]['collectCouponCode'];
                            newlist.push(result.data.result[i][0]);
                        }
                    }
                    that.couponList = newlist;
                }
            });
            this.$store.dispatch('getCollectInfo'); // 更新获得优惠券的收藏信息，用于筛选收藏按钮
        },
        changePage(id) {
            if (id === 0) {
                this.loadShopPage();
            } else {
                this.loadCouponPage();
            }
            for (var i = 0; i < 2; i++) {
                this.$data.tabs[i].isActive = false;
            };
            this.$data.tabs[id].isActive = true;
        },
        goBack () {
            this.$router.go(-1);
        }
    },
    created () {
        this.loadCouponPage();
        this.loadShopPage();
    },
    components: {
        'v-nav': nav,
        shoplist,
        indexcoupon,
        header2fix
    }
};



